import React from 'react';
import { Helmet } from 'react-helmet';
import { Container } from 'react-bootstrap';
import { Card, CardContent, CardMedia, Typography } from '@material-ui/core';
import { Link } from 'react-router-dom';

import { HeaderImageUrl, content } from '../special/Values';
import HeaderImage from '../components/HeaderImage';

// Import CSS
import '../css/page.css';

function Content() {
    return (
        <div>
            <Helmet>
                <title>Content | Grandis Library</title>
            </Helmet>
            <HeaderImage imageUrl={`${HeaderImageUrl.ristonia}.jpg`}/>
            <h1 className="page-title">Content</h1>
            <h2 className="page-subtitle">Gameplay Related</h2>
            <div className="content-container">
                {
                    content.gameplay.map(el => 
                            <Card className="content-card">
                                <Link to={`content/${el[0]}`}><CardMedia className="content-card-image" image={`${process.env.PUBLIC_URL}/thumbnail/${el[0]}.png`} title={el[1]}/></Link>
                                <CardContent>
                                    <Typography gutterBottom variant="h5" component="h2">
                                        <Link to={`content/${el[0]}`}>{el[1]}</Link>
                                    </Typography>
                                    <div class="card-text-container">
                                        <Typography className="typo-text" color="textPrimary" variant="body2" component="p">{el[2]}</Typography>
                                    </div>
                                </CardContent>
                            </Card>
                    )
                }
            </div>
            <h2 className="page-subtitle">Class Related</h2>
            <div className="content-container">
                {
                    content.class.map(el => 
                            <Card className="content-card">
                                <Link to={`content/${el[0]}`}><CardMedia className="content-card-image" image={`${process.env.PUBLIC_URL}/thumbnail/${el[0]}.png`} title={el[1]}/></Link>
                                <CardContent>
                                    <Typography gutterBottom variant="h5" component="h2">
                                        <Link to={`content/${el[0]}`}>{el[1]}</Link>
                                    </Typography>
                                    <div class="card-text-container">
                                        <Typography className="typo-text" color="textPrimary" variant="body2" component="p">{el[2]}</Typography>
                                    </div>
                                </CardContent>
                            </Card>
                    )
                }
            </div>
            <h2 className="page-subtitle">Equipment Related</h2>
            <div className="content-container">
                {
                    content.equipment.map(el => 
                            <Card className="content-card">
                                <Link to={`content/${el[0]}`}><CardMedia className="content-card-image" image={`${process.env.PUBLIC_URL}/thumbnail/${el[0]}.png`} title={el[1]}/></Link>
                                <CardContent>
                                    <Typography gutterBottom variant="h5" component="h2">
                                        <Link to={`content/${el[0]}`}>{el[1]}</Link>
                                    </Typography>
                                    <div class="card-text-container">
                                        <Typography className="typo-text" color="textPrimary" variant="body2" component="p">{el[2]}</Typography>
                                    </div>
                                </CardContent>
                            </Card>
                    )
                }
            </div>
        </div>
    )
}

export default Content
